import * as Types from '../constant/ActionTypes'
var data = JSON.parse(localStorage.getItem('CARD'));
var initialState = data ? data : [];

const findIndex = (newState, productInput) => {
    let result = -1;
    if (newState.length > 0) {
        newState.forEach((product, index) => {

            if (product.product.id === productInput.id) {
                result = index;
            }
        });
        return result;
    }

};

const card = (state = initialState, action) => {
    switch (action.type) {
        case Types.ADD_TO_CARD: {

            let newState = [...state];
            let index = findIndex(newState, action.product);
            if (newState.length > 0) {
                if (index == -1) {
                    newState.push({
                        product: action.product,
                        quantity: 1
                    });
                } else {
                    newState[index].quantity += action.quantity;
                }
            } else {
                newState.push({
                    product: action.product,
                    quantity: 1
                });
            }
            localStorage.setItem('CARD', JSON.stringify(newState));
            return newState;
        }
        case Types.DELETE_PRODUCT_IN_CARD: {
            let newState = [...state];
            let index = findIndex(newState, action.product.id);
            newState.splice(index, 1);

            localStorage.setItem('CARD', JSON.stringify(newState));
            return newState;
        }
        case Types.UPDATE_QUANTITY: {
            let newState = [...state];
            console.log(action);
            let index = newState.findIndex(product => {
                return product.product.id === action.id
            })
            console.log(index);
            if (index !== -1) {
                if (newState[index].quantity == 1 && action.quantity == -1) {
                    newState.splice(index, 1);
                } else {
                    newState[index].quantity += action.quantity;
                }

            }
            localStorage.setItem('CARD', JSON.stringify(newState));
            return newState;
        }
        default: return [...state];
    }
}

export default card