import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types"
import Card from "./../components/Card"
import * as Message from './../constant/Message'
import CardItem from './../components/CardItem'
import CardResult from './../components/CardResult'
import * as Action from './../actions/action'

class CardContainer extends Component {
    render() {
        var { card } = this.props;
        return (
            <Card>
                {this.ShowCardItems(card)}
                {this.showTotalAmount(card)}
            </Card>
        )
    }
    
    ShowCardItems = (card) => {
        var { onDeleteProductInCard, onChangeMessage, onUpdateProductInCard } = this.props;
        let result =
            <tr>
                <td>
                    {Message.MSG_CARD_EMPTY}
                </td>
            </tr>;
        if (card.length > 0) {
            result = card.map((item, index) => {
                return (
                    <CardItem
                        key={index}
                        item={item}
                        onDeleteProductInCard={onDeleteProductInCard}
                        onChangeMessage={onChangeMessage}
                        onUpdateProductInCard={onUpdateProductInCard}
                    />
                )
            })
        }
        return result
    }
    showTotalAmount = (products) => {
        var result = null;
        if (products.length > 0) {
            result = <CardResult products={products} />
        }
        return result;
    }
}

CardContainer.propTypes = {
    card: PropTypes.arrayOf(
        PropTypes.shape({
            product: PropTypes.shape({
                id: PropTypes.number.isRequired,
                name: PropTypes.string.isRequired,
                image: PropTypes.string.isRequired,
                description: PropTypes.string.isRequired,
                price: PropTypes.number.isRequired,
                inventory: PropTypes.number.isRequired,
                rating: PropTypes.number.isRequired
            }).isRequired,
            quantity: PropTypes.number.isRequired
        })
    ).isRequired
}

const mapStateToProps = (state) => {
    return {
        card: state.card
    }
}

const mapDispatchToProps = (dispatch, props) => {
    return {
        onDeleteProductInCard: (product) => { dispatch(Action.actDeleteProductInCard(product)) },
        onChangeMessage: (message) => { dispatch(Action.actChangeMessage(message)) },
        onUpdateProductInCard: (id, quantity) => { dispatch(Action.actUpdateProductInCard(id, quantity)) }

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CardContainer);
