import React, { Component } from 'react'
import * as message from './../constant/Message'
class CardItem extends Component {
	
	render() {
		let { item } = this.props;
		return (
			<tr>
				<th scope="row">
					<img src={item.product.image}
						alt="" className="img-fluid z-depth-0" />
				</th>
				<td>
					<h5>
						<strong>{item.product.name}</strong>
					</h5>
				</td>
				<td>{item.product.price} $</td>
				<td className="center-on-small-only">
					<span className="qty">{item.quantity}</span>
					<div className="btn-group radio-group" data-toggle="buttons">
						<label className="btn btn-sm btn-primary
                                                btn-rounded waves-effect waves-light"
							onClick={() => this.onUpdateQuantity(item.product.id, - 1)}
						>
							<a>—</a>
						</label>
						<label className="btn btn-sm btn-primary
                                                btn-rounded waves-effect waves-light"
							onClick={() => this.onUpdateQuantity(item.product.id, + 1)}
						>
							<a>+</a>
						</label>
					</div>
				</td>
				<td>{item.product.price * item.quantity} $</td>
				<td>
					<button
						type="button"
						className="btn btn-sm btn-primary waves-effect waves-light"
						data-toggle="tooltip"
						data-placement="top"
						title=""
						data-original-title="Remove item"
						onClick={() => this.onDelete(item.product, message)}
					>
						X
                     </button>
				</td>
			</tr>
		)
	}
	onDelete = (product, message) => {
		let { onDeleteProductInCard, onChangeMessage } = this.props;
		onDeleteProductInCard(product);
		onChangeMessage(message.MSG_DELETE_PRODUCT_IN_CARD_SUCCESS);
	}
	onUpdateQuantity = (id, quantity) => {
		
		this.props.onUpdateProductInCard(id, quantity);
		this.props.onChangeMessage(message.MSG_UPDATE_CARD_SUCCESS);
	}
}
export default (CardItem)
