import React, { Component } from 'react';
import * as Message from './../constant/Message'
class Product extends Component {

	render() {
		var { product } = this.props;

		return (
			<div className="col-lg-4 col-md-6 mb-r">
				<div className="card text-center card-cascade narrower">
					<div className="view overlay hm-white-slight z-depth-1">
						<img src={product.image}
							className="img-fluid" alt={product.name} />
						<a>
							<div className="mask waves-light waves-effect waves-light"></div>
						</a>
					</div>
					<div className="card-body">
						<h4 className="card-title">
							<strong>
								<a>{product.name}</a>
							</strong>
						</h4>
						<ul className="rating">

							{this.showRating(product.rating)}

						</ul>
						<p className="card-text">
							{product.description}
						</p>
						<div className="card-footer">
							<span className="left">{product.price} $</span>
							<span className="right">
								<a className="btn-floating blue-gradient"
									data-toggle="tooltip"
									data-placement="top"
									title=""
									data-original-title="Add to Cart"
									onClick={() => this.onAddToCard(product)}
								>
									<i className="fa fa-shopping-cart"></i>
								</a>
							</span>
						</div>
					</div>
				</div>
			</div>

		)
	}
	showRating(rating) {
		var star = [];
		// tạo ra các sao đánh giá sản phẩm
		if (rating > 0) {
			for (let i = 0; i < rating; i++) {
				star.push(<li key={i} className=""><i className="fa fa-star"></i></li>)
			}
			for (let i = 0; i < 5 - rating; i++) {
				star.push(<li key={-i - 1}><i className="fa fa-star-o"></i></li>)
			}
		}
		
		return star
	}

	onAddToCard = (product) => {
		this.props.onAddToCard(product);
		this.props.onChangeMessage(Message.MSG_ADD_TO_CARD_SUCCESS)
	}
}
export default (Product)
